'use client'

import { useState, useEffect } from 'react'
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"

interface Subscription {
  id: string
  customerId: string
  customerName: string
  customerAddress: string
}

const defaultSubscriptions: Subscription[] = [{
  id: "1",
  customerId: "1",
  customerName: "Edgar Eidsheim",
  customerAddress: "Bassengvegen 18"
}]

export default function NyInspeksjon() {
  const [subscriptions, setSubscriptions] = useState<Subscription[]>([])
  const [selectedAddress, setSelectedAddress] = useState('')
  const [season, setSeason] = useState('')
  const [images, setImages] = useState<File[]>([])
  const [imageUrls, setImageUrls] = useState<string[]>([])
  const [date, setDate] = useState('')
  const [isClient, setIsClient] = useState(false)

  useEffect(() => {
    setIsClient(true)
    // Hent abonnementer fra localStorage, eller bruk standarddata hvis ingen finnes
    const savedSubscriptions = JSON.parse(localStorage.getItem('subscriptions') || 'null')
    if (!savedSubscriptions) {
      localStorage.setItem('subscriptions', JSON.stringify(defaultSubscriptions))
      setSubscriptions(defaultSubscriptions)
    } else {
      setSubscriptions(savedSubscriptions)
    }
  }, [])

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const newImages = Array.from(e.target.files)
      setImages(prev => [...prev, ...newImages])
      
      // Lag forhåndsvisnings-URLer for bildene
      const newUrls = newImages.map(file => URL.createObjectURL(file))
      setImageUrls(prev => [...prev, ...newUrls])
    }
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    const inspection = {
      id: Date.now().toString(),
      address: selectedAddress,
      season: season,
      date: date,
      images: imageUrls,
      roofCondition: (e.target as any).roofCondition.value,
      notes: (e.target as any).notes.value,
      status: 'planlagt'
    }

    // Lagre inspeksjonen
    const existingInspections = JSON.parse(localStorage.getItem('inspections') || '[]')
    localStorage.setItem('inspections', JSON.stringify([...existingInspections, inspection]))

    // Simuler SMS-sending (dette ville koble til en faktisk SMS-tjeneste i produksjon)
    if ((e.target as any).sendSms.checked) {
      console.log('SMS ville blitt sendt:', {
        to: 'kundens nummer',
        message: `Vi kommer for å vedlikeholde taket ditt den ${date}`
      })
    }

    alert('Inspeksjon planlagt!')
    window.location.href = '/inspeksjoner'
  }

  return (
    <div className="max-w-2xl mx-auto p-4">
      <h1 className="text-2xl font-bold mb-6">Planlegg takvedlikehold</h1>

      {isClient && (
        <form onSubmit={handleSubmit} className="space-y-6">
          <Card className="p-6">
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium mb-1">Velg adresse</label>
                <select
                  className="w-full p-2 border rounded"
                  value={selectedAddress}
                  onChange={(e) => setSelectedAddress(e.target.value)}
                  required
                >
                  <option value="">Velg adresse...</option>
                  {subscriptions.map((sub) => (
                    <option key={sub.id} value={sub.customerAddress}>
                      {sub.customerAddress}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">Sesong</label>
                <select
                  className="w-full p-2 border rounded"
                  value={season}
                  onChange={(e) => setSeason(e.target.value)}
                  required
                >
                  <option value="">Velg sesong...</option>
                  <option value="vår">Vår</option>
                  <option value="høst">Høst</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">Dato</label>
                <Input
                  type="date"
                  value={date}
                  onChange={(e) => setDate(e.target.value)}
                  required
                />
              </div>
            </div>
          </Card>

          <Card className="p-6">
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium mb-1">Taktilstand</label>
                <select
                  name="roofCondition"
                  className="w-full p-2 border rounded"
                  required
                >
                  <option value="">Velg tilstand...</option>
                  <option value="god">God</option>
                  <option value="normal">Normal</option>
                  <option value="dårlig">Dårlig</option>
                  <option value="kritisk">Kritisk</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">Last opp bilder</label>
                <Input
                  type="file"
                  accept="image/*"
                  multiple
                  onChange={handleImageUpload}
                />
                <div className="grid grid-cols-3 gap-2 mt-2">
                  {imageUrls.map((url, index) => (
                    <img
                      key={index}
                      src={url}
                      alt={`Bilde ${index + 1}`}
                      className="w-full h-24 object-cover rounded"
                    />
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">Notater</label>
                <Textarea
                  name="notes"
                  placeholder="Beskriv takets tilstand og eventuelle problemer..."
                />
              </div>

              <div className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  name="sendSms"
                  id="sendSms"
                  className="rounded border-gray-300"
                />
                <label htmlFor="sendSms">
                  Send SMS til kunde om planlagt vedlikehold
                </label>
              </div>
            </div>
          </Card>

          <div className="flex justify-end space-x-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => window.location.href = '/inspeksjoner'}
            >
              Avbryt
            </Button>
            <Button type="submit">
              Planlegg inspeksjon
            </Button>
          </div>
        </form>
      )}
    </div>
  )
} 